export const DrawerOption = (rol) => {
    switch (rol) {
        case socio:
            return arrayOptionSocio;
        case validador:
            return arrayOptionValidador;
        case informador:
            return arrayOptionSocio;
        default:
            return arrayOptionSocio;
    }
}

