import React, { useEffect, useState } from 'react';
import { getInfoRegionProject } from "./FuncionesCharts";
import { Treemap, ResponsiveContainer } from "recharts";
import { Grid } from "@material-ui/core";
import { colorsCharts } from "../../../assets/app_const/ColorsConst";

export const ProjectRegion = () => {
    const [info, setInfo] = useState([]);



    useEffect(() => {
        let mounted = true;

        if (mounted) {
            getInfoRegionProject(setInfo)
        }
        return () => {
            mounted = false
        };
    }, [setInfo]);


    return <Grid container style={{ height: 500 }}>
        <ResponsiveContainer width="100%" height="100%">
            <Treemap dataKey="size" onClick={(entry)=>console.log(entry.id)} colorPanel={colorsCharts} width={300} height={200} data={info} isAnimationActive={false} stroke="#fff" />
        </ResponsiveContainer>
    </Grid>


}




