import React, {useState} from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import logoCTES from "../../../assets/imgs/logoCTESnavbar.svg";
import Grid from "@material-ui/core/Grid";
import {Strings} from "../../../assets/app_const/AppConstants";
import {TextColor} from "../../../assets/app_const/ColorsConst";
import {useDispatch, useSelector } from "react-redux";
import {IconButton, Menu, MenuItem} from "@material-ui/core";
import {AccountCircle} from "@material-ui/icons";
import {createActionDrawerOpen, createActionLogOut} from "../../../views/LoginView/reducer/actionReducer";
import {NavLink, useHistory} from "react-router-dom"
import {pathFront} from "../../../views/LoginView/app_const/AppConst";

export const Navbar = () => {
    const {Token, FullName} = useSelector((state) => state.auth);
    const [OpenMenu, setOpenMenu] = useState(null);
    const dispatch = useDispatch()
    const history=useHistory();

    const handleCloseMenu = () => {
        setOpenMenu(false);
    }
    const handleOpenMenu = (event) => {
        dispatch(createActionDrawerOpen());
    };

    const handleLogOut = () => {
        handleCloseMenu();
        history.push(pathFront.home)
        dispatch(createActionLogOut());
    }


    return (
        <>
            <AppBar
                position="sticky"
                style={{
                    background: "#000000",
                }}
            >
                <Toolbar>
                    <Grid container direction="row" justify="center">
                        <Grid
                            container
                            item
                            xl={12}
                            justify={Token ? "flex-start" : "center"}
                        >
                            <NavLink to="/">
                                <img src={logoCTES} alt="logo"/>

                            </NavLink>

                        </Grid>
                        <Grid
                            container
                            item
                            xl={12}
                            justify={Token ? "flex-start" : "center"}
                        >
                            <NavLink to="/" style={{textDecoration:"inherit",color:"inherit"}}>
                                <Typography
                                    variant="body2"
                                    style={{color: TextColor.TextDisable}}
                                >
                                    {Strings.title_app}
                                </Typography>
                            </NavLink>
                        </Grid>
                    </Grid>

                    {Token ? (
                        <IconButton aria-controls="simple-menu" edge="start" color="inherit" aria-label="menu"
                                    aria-haspopup="true" onClick={handleOpenMenu}>
                            <AccountCircle/> {" " + FullName}
                        </IconButton>

                    ) : <></> }

                </Toolbar>

            </AppBar>

            <Menu
                id="simple-menu"
                keepMounted
                anchorEl={OpenMenu}
                open={Boolean(OpenMenu)}
                onClose={handleCloseMenu}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                <MenuItem onClick={handleCloseMenu}>Profile</MenuItem>
                <MenuItem onClick={handleCloseMenu}>My account</MenuItem>
                <MenuItem onClick={handleLogOut}>Logout</MenuItem>
            </Menu>
        </>
    );
};
